
.movieList {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(420px, 1fr));
  grid-gap: 16px;
  margin-top: 60px;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.movieNav {
  color: aliceblue;
  text-decoration: none;
  font-size: 15px;
  white-space: nowrap;
}

.moviePoster {
  width: 100%;
  height: 96%;
  object-fit: cover;
}